* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

.home,
.services,
.about,
.contact {
  display: flex;
  align-items: center;
  justify-content: center;
/*  font-size: 3rem;*/
}

.about {
  background-image: url('components/images/img3.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  text-shadow: 1px 2px 1px #3b3838;
/*  font-size: 100px;*/
  display: flex;
  justify-content: center;
}



.aboutwrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.aboutimage {
  border-radius: 12px;
  margin: 12px;
  height: auto;
  width: 300px;
}

.aboutimage {
  border-radius: 12px;
  margin: 12px;
}

.contact {
  background-image: url('components/images/img2.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  /*font-size: 100px;*/
  flex-direction: column;
  height: 50vh;
  text-shadow: 1px 2px 1px #3b3838;
}

.contactitem {
  display: flex;
  justify-content: space-around;
  margin: 12px;
}

h4 > a {
  margin-left: 8px;
}

.contactinfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100px;
}

.contactlink {
  display: flex;
  align-self: flex-end;
}

.contactlogo {
  margin-left: 16px;
}
/*.contact {
  background-image: url('components/images/img8.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}*/

.infocontainer {
  display: flex;
  padding: 12px;
  align-items: center;
  background-color:#2b2b2b;
  color: white;
}

.contactheader > h1 {
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contactheader > h3 {
  margin: 12px 16px 40px 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contactlink {
  color:#5bbab4;
}

.abouttext {
  display: flex;
  justify-content: center;
  align-items: center;
}

.aboutintro {
  margin: 12px;
  width: 50%;
}

@media screen and (max-width: 960px) {
  .about {
    font-size: 0.8rem;
  }
  .aboutimage {
    width: 200px;
    height: auto;
  }

   .contactitem {
    font-size: 0.85rem;
  }

}

@media screen and (max-width: 768px) {
  .about {
    font-size: 0.7rem;
  }
  .aboutimage {
    width: 175px;
    height: auto;
  }

  .contactitem {
    font-size: 0.7rem;
  }
}

@media screen and (max-width: 480px) {
  .contactitem {
    font-size: 0.6rem;
  }
}